<template>
  <div
    v-if="albumInfo"
    @click="clickTitleRow"
    class="album-header-container f-row-between"
  >
    
    <div
      
      class="cover"
      :style="{ 'background-image': `url( ${albumInfo.cover} )` }"
    >
      <p class="content-type">
        {{ albumInfo.contentType | fltTemplateName }}
      </p>
      <p v-if="albumInfo.lastPlayTime" class="last-time">
        {{ albumInfo.lastPlayTime | fltTimeInterval }}听过
      </p>
    </div>

    <div class="content f-col-between">
      <div class="title">
        <div
          class="first-title f-row-between-c"
        >
          <p class="title-text two-ellipsis">
            {{ albumInfo.name }}
          </p>
          <img v-if="$route.name === 'album-catalogue'" src="@/assets/images/play_music_more.png" alt="">
        </div>
        <p class="album-intro two-ellipsis">
          {{ albumInfo.intro }}
        </p>
      </div>
      <div v-if="albumInfo.playUv > 0" class="detail">
        {{ `${albumInfo.playUv}人听过` }}
      </div>
      <div
        v-if="albumInfo.messageCount > 0"
        @click.stop="clickTotalMessage"
        class="message-box f-c"
      >
        {{ `留言(${ albumInfo.messageCount > 9999 ? '9999+' : albumInfo.messageCount })` }}
      </div>
    </div>

  </div>
</template>

<script>
export default {
  props: {
    albumInfo: {
      type: Object,
      default: null
    },
  },
  
  data() {
    return {
      
    }
  },

  methods: {
    // todo 跳转至查看全部留言页面
    clickTotalMessage() {
      this.$router.push({
        name: 'total-message',
        query: {
          albumId: this.albumInfo._id
        }
      })
    },
    
    // todo 点击标题行
    clickTitleRow() {
      this.$emit( 'clickTitleRow' );
    }
  }
}
</script>

<style lang="scss" scoped>
.album-header-container {
  width: 100%;
  height: 120px;

  .cover {
    width: 90px;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 5px;
    position: relative;

    .content-type, .last-time {
      box-sizing: border-box;
      padding: 2.5px 5px;
      background: rgba(0,0,0,0.3);
      border-radius: 5px 0 5px 0;
      font-size: 11px;
      color: rgba(255,255,255,1);
    }
    .content-type {
      position: absolute;
      top: 0;
      right: 0;
    }
    .last-time {
      position: absolute;
      bottom: 0;
      left: 0;
    }
  }
  .content {
    width: calc(100% - 105px);
    box-sizing: border-box;
    position: relative;
    
    .title {
      .first-title {
        margin-bottom: 5px;

        .title-text {
          font-size: 18px;
          font-weight: 600;
          color: rgba(51,51,51,1);
          width: 90%;
        }
        img {
          width: 7px;
          height: 10px;
          position: relative;
          left: -5px;
        }
      }
      .album-intro {
        width: 100%;
        font-size: 14px;
        color: rgba(153,153,153,1);
        line-height: 1.5;
      }
    }
    .detail {
      font-size: 13px;
      color: rgba(170,170,170,1);
    }
    .message-box {
      background:rgba(244,186,65,1);
      border-radius: 15px;
      position: absolute;
      bottom: 1px;
      right: 0;
      padding: 4px 9px;
      font-size: 13px;
      color: rgba(255,255,255,1);
    }
  }
}
</style>


