import { albumIsNeedBuy } from './index';
import { router } from '../router/index';

// todo 访问专辑的跳转，如果不需要购买进入专辑目录页，否则进入专辑介绍页
export function toAlbumPage(chargeType, isBuy, albumId) {
  const pathName = albumIsNeedBuy(chargeType, isBuy) ? 'album-introduction' : 'album-catalogue';
  router.push({
    name: pathName,
    query: {
      albumId
    }
  });
}


