import { request } from '../request';
import { getErrorMsg } from '../interfaceRules/errorMsg';
import { PAGE_NUM, PAGE_SIZE } from '../constantParams/defaultPage';

/**
 * todo 按栏目类型，分页获取推荐栏目
 * @param { String } categoryType 栏目类型 => 见 utils/category.js
 * @param { Number } pageNum 
 * @param { Number } pageSize 
 */
export function apiCategoryRecommendList(categoryType, pageNum = PAGE_NUM, pageSize = PAGE_SIZE) {
  return request({
    method: 'GET',
    url: `/category/recommendlist-by-categorytype/${categoryType}/${pageNum}/${pageSize}`
  }).then(result => {
    if (result.code === '0') {
      return Promise.resolve({
        type: 'success',
        data: result.data
      })
    } else {
      const message = getErrorMsg(result.code);
      return Promise.resolve({
        type: 'fail',
        message: result.message || message
      })
    }
  }).catch(err => {
    console.log( '接口错误: ', err );
  })
}

