/**
 * todo 通过多个专辑id（专辑id之间用“，”分隔），查询条目列表
 * @param { Array -> String } albumIds 专辑 id 们，接口接收的是 String，下面通过 [].prototype.join(',') 把数组连起来
 * @param { Number } pageNum 
 * @param { Number } pageSize 
 */
export function apiAlbumItemListByAlbumIds(albumIds, pageNum = PAGE_NUM, pageSize = PAGE_SIZE) {
  albumIds = albumIds.join(','); // 拼接所有专辑id
  return request({
    method: 'GET',
    url: `/albumitem/list-by-albumids/${albumIds}/${pageNum}/${pageSize}`
  }).then(result => {
    if (result.code === '0') {
      return Promise.resolve({
        type: 'success',
        data: result.data
      })
    } else {
      const message = getErrorMsg(result.code);
      return Promise.resolve({
        type: 'fail',
        message: result.message || message
      })
    }
  }).catch(err => {
    console.log( '接口错误: ', err );
  })
}

