<template>
  <div
    @click="clickSectionLink"
    v-if="message"
    :style="{ 'background-color': backColor }"
    class="section-link-container f-row-between"
  >
    
    <div
      v-if="message.refAlbumItem"
      :style="{ 'background-image': `url( ${message.refAlbum.cover} )` }"
      class="cover"
    />
    <div class="content f-col-between">
      <p class="title one-ellipsis">
        {{ message.refAlbumItem.title }}
      </p>
      <p v-if="message.refAlbumItem" class="tag">
        {{ message.refAlbum.name }}
      </p>
    </div>

  </div>
</template>

<script>
import { toAlbumPage } from '@/utils/proxyRouteSkip';

export default {
  props: {
    backColor: {
      type: String,
      default: 'rgba(249,249,249,1)'
    },
    message: {
      type: Object,
      default: null
    }
  },

  methods: {
    clickSectionLink() {
      if (!this.message.albumId) return ;

      const albumInfo = this.message.refAlbum;
      toAlbumPage(albumInfo.chargeType, albumInfo.isBuy, albumInfo.albumId);
    }
  }
}
</script>

<style lang="scss" scoped>
.section-link-container {
  width: 100%;
  height: 60px;
  border-radius: 4px;

  .cover {
    width: 45px;
    height: 60px;
    border-radius: 4px 0 0 4px;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
  }
  .content {
    width: calc(100% - 45px);
    box-sizing: border-box;
    padding: 10px 15px;

    .title {
      width: 98%;
      font-size: 14px;
      font-weight: 600;
      color: rgba(102,102,102,1);
    }
    .tag {
      font-size: 13px;
      color: rgba(153,153,153,1);
    }
  }
}
</style>


