<template>
  <div class="tabbar-container flx">

    <div @click="switchTab( '/morning-parents' )" class="tab f-col-between-c">
      <img v-if="$route.path === '/morning-parents'" src="@/assets/images/tabbar_morning_sel.png" alt="">
      <img v-else src="@/assets/images/tabbar_morning_nor.png" alt="">
      <p :class="[ $route.path === '/morning-parents'? 'choosed' : '' ]">父母听</p>
    </div>

    <div @click="switchTab( '/good-night-baby' )" class="tab f-col-between-c">
      <img v-if="$route.path === '/good-night-baby'" src="@/assets/images/tabbar_night_sel.png" alt="">
      <img v-else src="@/assets/images/tabbar_night_nor.png" alt="">
      <p :class="[ $route.path === '/good-night-baby' ? 'choosed' : '' ]">孩子听</p>
    </div>

    <div @click="switchTab( '/purchased' )" class="tab f-col-between-c">
      <img v-if="$route.path === '/purchased'" src="@/assets/images/tabbar_study_sel.png" alt="">
      <img v-else src="@/assets/images/tabbar_study_nor.png" alt="">
      <p :class="[ $route.path === '/purchased' ? 'choosed' : '' ]">已购</p>
    </div>

    <div @click="switchTab( '/my' )" class="tab f-col-between-c">
      <img v-if="$route.path === '/my'" src="@/assets/images/tabbar_profile_sel.png" alt="">
      <img v-else src="@/assets/images/tabbar_profile_nor.png" alt="">
      <p :class="[ $route.path === '/my' ? 'choosed' : '' ]">个人中心</p>
    </div>

  </div>
</template>

<script>
export default {
  methods: {
    switchTab( path ) {
      if (path === this.$route.path) return ;
      
      this.$router.replace( path );
    }
  }
}
</script>

<style lang="scss" scoped>
.tabbar-container {
  background-color: #fff;
  width: 100%;
  box-sizing: border-box;
  padding: 6px 15px;
  height: 56px;

  .tab {
    width: 25%;

    img {
      width: 28px;
    }
    p {
      font-size: 10px;
      color: rgba(170,170,170,1);
    }
    .choosed {
      color: #222222;
    }
  }
}
</style>


