<template>
  <div
    @click="toMessageDetailPage"
    v-if="commentList && commentList.length > 0"
    class="reply-list-container"
  >
    
    <div
      v-for="(comment, commentIndex) in commentList"
      :key="comment._id"
    >
      <div
        v-if="commentIndex < hideLimit"
        class="reply"
      >
        <p class="content">
          <span class="nickname">
            {{ comment.nickName }}
          </span>
          {{ `：${comment.content}` }}
        </p>
        <SectionLink backColor="#fff" v-if="isShowReplyLink" style="margin-top: 10px" />
      </div>
    </div>

    <p
      v-if="isShowMoreLink && commentList.length > hideLimit"
      class="more-reply"
    >
      共{{ commentCount }}条回复 >
    </p>

  </div>
</template>

<script>
import SectionLink from './SectionLink.vue';

export default {
  props: {
    isShowReplyLink: {
      type: Boolean,
      default: false
    },
    hideLimit: {
      type: Number,
      default: 2
    },
    isShowMoreLink: {
      type: Boolean,
      default: true
    },
    commentList: {
      type: Array,
      default: []
    },
    messageId: {
      type: String,
      default: null
    },
    commentCount: {
      type: Number,
      default: 3
    }
  },

  components: {
    SectionLink
  },

  methods: {
    // todo 跳转到留言详情页面
    toMessageDetailPage() {
      this.$router.push({
        name: 'detail-message',
        query: {
          messageId: this.messageId
        }
      });
    }
  }
}
</script>

<style lang="scss" scoped>
.reply-list-container {
  width: 100%;
  padding: 10px 7.5px 10px 10px;
  box-sizing: border-box;
  background: rgba(247,247,247,1);
  border-radius: 1px;

  .reply {
    .content {
      font-size: 13px;
      color: #666666;
      line-height: 1.5;
      
      .nickname {
        color:rgba(31,110,208,1);
      }
    }
  }

  .more-reply {
    color:rgba(31,110,208,1);
    font-size: 13px;
    margin-top: 8px;
  }
}
</style>


