<template>
  <div class="message-list-container">

    <div
      v-for="(message, messageIndex) in messageList"
      :key="message._id"
      :class="['message-region', messageIndex > 0 ? 'm-top-25' : '']"
    >
      <div class="main-area flx">
        <div class="avatar-area">
          <img :src="message.avatar" alt="">
        </div>
        <div class="content-area">
          <div class="right-content f-row-between">
            <div class="info f-col-between">
              <div class="one-row f-i-c">
                <p class="nick">{{ message.nickName }}</p>
                <!-- // ? 待开发 -->
                <!-- <span class="identity f-c">
                  作者
                </span> -->
              </div>
              <div class="two-row f-i-c">
                <p>
                  {{ message.msgTime }}
                </p>
                <!-- // ? 待开发 -->
                <!-- <span class="dot">
                  ·
                </span>
                <p>
                  已打卡5天
                </p> -->
              </div>
            </div>
            <div
              @click="switchPraise(message, messageIndex)"
              class="like f-row-end-c"
            >
              <span>
                {{ message.praiseUv }}
              </span>
              <img
                :src="message.isPraise ? praise.yes : praise.no"
                alt=""
              >
            </div>
          </div>
          <div
            @click="toReplyPage(message)"
            class="comment"
          >
            <span v-if="message.leaveMessage">
              回复 <span class="color-blue">{{ message.leaveMessage.nickName }}</span>：
            </span>
            {{ message.content }}
          </div>
          
          <ReplyList
            v-if="isShowReply"
            :hideLimit="hideLimit"
            :isShowMoreLink="isShowMoreLink"
            :isShowReplyLink="isShowReplyLink"
            :commentList="message.comments"
            :messageId="message._id"
            :commentCount="message.commentCount"
            style="margin-top: 15px"
          />

          <!-- // -- 如果组件渲染的是留言 -->
          <SectionLink
            :message="message"
            v-if="!isRenderComment && isShowCommentLink && message && message.refAlbumItem"
            style="margin-top: 15px"
          />

          <!-- // -- 如果组件渲染的是评论 -->
          <SectionLink
            :message="message.leaveMessage"
            v-if="isRenderComment && isShowCommentLink && message && message.leaveMessage && message.leaveMessage.refAlbumItem"
            style="margin-top: 15px"
          />
          
        </div>
      </div>
      <div v-if="messageIndex < messageList.length - 1" class="split-1" />
    </div>
    
  </div>
</template>

<script>
import SectionLink from './SectionLink.vue';
import ReplyList from './ReplyList.vue';
import { apiLeaveMessagePraise } from '@/service/api/leaveMessage';
import { apiCommentPraise } from '@/service/api/comment';
import Vue from 'vue';
import { Toast } from 'vant';

Vue.use(Toast);

export default {
  props: {
    isShowCommentLink: { // 是否显示留言所在小节链接
      type: Boolean,
      default: true
    },
    isShowReply: { // 是否显示回复
      type: Boolean,
      default: true
    },
    hideLimit: { // 回复模块最多显示几条
      type: Number,
      default: 2
    },
    messageList: {
      type: Array,
      default: []
    },
    isShowReplyLink: {
      type: Boolean,
      default: false
    },
    isShowMoreLink: {
      type: Boolean,
      default: true
    },
    isRenderComment: { // -- 是否渲染的是评论，如果是评论数据结构会不一样，页面上做了分别渲染
      type: Boolean,
      default: false
    }
  },

  components: {
    SectionLink,
    ReplyList
  },
  
  data() {
    return {
      praise: {
        yes: require('../../assets/images/comment_like.png'),
        no: require('../../assets/images/comment_no_like.png'),
      }
    }
  },

  methods: {
    // todo 跳转至回复留言或评论的页面
    toReplyPage(message) {
      if (this.$route.name === 'my-publish') return ;
      this.$router.push({
        name: 'reply-message',
        query: {
          userNick: message.nickName,
          msgId: message._id,
          msgUserId: message.userId,
          srcCommentId: ''
        }
      })
    },

    // todo 判断点赞或者取消赞的是留言还是评论 -> 再去请求接口执行点、取消的行为
    switchPraise(message, messageIndex) {
      if (this.isRenderComment) {
        this.switchPraiseComment(message, messageIndex);
      } else {
        this.switchPraiseMessage(message, messageIndex);
      }
    },
    // todo 切换「留言」点赞状态，发给父组件
    async switchPraiseMessage(message, messageIndex) {
      const switchPraiseResponse = await apiLeaveMessagePraise({
        msgId: message._id,
        isPraise: !message.isPraise
      });
      if (!switchPraiseResponse || switchPraiseResponse.type === 'fail') return Toast('服务器错误，请联系客服');
      this.$emit('switchPraise', messageIndex);
    },
    // todo 切换「评论」点赞状态，发给父组件
    async switchPraiseComment(message, messageIndex) {
      console.log( '点赞评论', message, messageIndex );
      const switchPraiseResponse = await apiCommentPraise({
        commentId: message._id,
        isPraise: !message.isPraise
      });
      if (!switchPraiseResponse || switchPraiseResponse.type === 'fail') return Toast('服务器错误，请联系客服');

      this.$emit('switchPraise', messageIndex);
    },
  }
}
</script>

<style lang="scss" scoped>
.message-list-container {
  width: 100%;

  .message-region {
    width: 100%;
    
    .main-area {
      width: 100%;
      margin-bottom: 25px;

      .avatar-area {
        width: 33px;
        
        img {
          width: 33px;
          height: 33px;
          border-radius: 50%;
        }
      }
      .content-area {
        width: calc(100% - 15px - 33px);
        margin-left: 15px;

        .right-content {
          
          .info {
            width: 65%;
            .one-row {
              .nick {
                font-size: 15px;
                font-weight: 600;
                color: rgba(51,51,51,1);
              }
              .identity {
                height: 17px;
                padding: 1px 3px;
                box-sizing: border-box;
                background: rgba(255,252,246,1);
                border: 1px solid rgba(244,186,65,1);
                border-radius: 2px;
                margin-left: 5px;
                font-size: 11px;
                color: rgba(244,186,65,1);
              }
            }
            .two-row {
              font-size: 12px;
              color: rgba(153,153,153,1);

              .dot {
                margin: 0 2px;
              }
            }
          }

          .like {
            width: 30%;

            span {
              font-size: 12px;
              color: rgba(153,153,153,1);
            }
            img {
              width: 15px;
              height: 15px;
              margin-left: 5px;
            }
          }
        }
        .comment {
          padding: 0 1px;
          margin-top: 13px;
          width: 100%;
          box-sizing: border-box;
          font-size: 14px;
          color: rgba(51,51,51,1);
          line-height: 1.5;

          .color-blue {
            color: #1F6ED0;
          }
        }
      }
    }
    .split-1 {
      width: 100%;
      height: 1px;
      background: rgba(238,238,238,1);
    }
  }

  .m-top-25 {
    margin-top: 25px;
  }
}
</style>


