import { request } from '../request';
import { getErrorMsg } from '../interfaceRules/errorMsg';
import { getMessageType } from '../constantParams/messageType';
import { PAGE_NUM, PAGE_SIZE } from '../constantParams/defaultPage';

/**
 * todo 保存留言板评论
 * @param { string } msgId 评论的留言id
 * @param { string } msgUserId 	被评论的留言的用户id
 * @param { string } content 	评论内容
 * @param { string } srcCommentId 原评论id，针对评论回复时有值
 */
export function apiSaveComment({ msgId, msgUserId, content, srcCommentId }) {
  return request({
    method: 'POST',
    url: '/comment/save',
    data: {
      msgId,
      msgUserId,
      content,
      srcCommentId
    }
  }).then(result => {
    if (result.code === '0') {
      return Promise.resolve({
        type: 'success',
        data: result.data
      })
    } else {
      const message = getErrorMsg(result.code);
      return Promise.resolve({
        type: 'fail',
        message: result.message || message
      })
    }
  }).catch(err => {
    console.log( '接口错误: ', err );
  })
}

