import { db } from './lowdb';

/**
 * todo 判断当前专辑是否需要付费
 * @param { string } chargeType 专辑的收费类型：全部免费(0) 会员免费(1) 全部收费(2)
 * @param { boolean } isBuy 
 */
export function albumIsNeedBuy(chargeType, isBuy) {
  chargeType = chargeType === null ? '0' : chargeType;
  let userInfo = db.get('userInfo').value();
  return (chargeType == 1 && !userInfo.isMember && !isBuy) || (chargeType == 2 && !isBuy)
}


