/**
 * todo 提交新订单（H5）（!没有的参数设置值为空字符串）
 * @param { string } province 省份
 * @param { string } city 城市
 * @param { string } area 区
 * @param { string } address 详细地址
 * @param { string } consigneeMobileNo 收件人手机号码
 * @param { string } consignee 收货人
 * @param { string } zipcode 邮政编码	
 * @param { string } leaveMessage 用户留言
 * @param { array } spus 见 SpuItem，购买的产品列表
 *    @param { string } spuId 商品id
 *    @param { string } skuId 规格id
 *    @param { string } quantity 数量
 *    @param { string } spuName 商品名称
 */
export function apiOrderSubmit({ province = '', city = '', area = '', address = '', consigneeMobileNo = '', consignee = '', zipcode = '', leaveMessage = '', spus = [] }) {
  return request({
    method: 'POST',
    url: '/h5/order/submit',
    data: {
      province,
      city,
      area,	
      address,
      consigneeMobileNo,
      consignee,
      zipcode,
      leaveMessage,
      spus
    }
  }).then(result => {
    if (result.code === '0') {
      return Promise.resolve({
        type: 'success',
        data: result.data
      })
    } else {
      const message = getErrorMsg(result.code);
      return Promise.resolve({
        type: 'fail',
        message: result.message || message
      })
    }
  }).catch(err => {
    console.log( '接口错误: ', err );
  })
}

