import { request } from '../request';
import { getErrorMsg } from '../interfaceRules/errorMsg';
import { getMessageType } from '../constantParams/messageType';
import { PAGE_NUM, PAGE_SIZE } from '../constantParams/defaultPage';

/**
 * todo 保存留言
 * @param { String } targetId 目标对象id，type = '0' 为 条目id，type = '2' 为专辑id
 * @param { String } type '留言': 0 '评论': 2，传「album 或者 section」就可以，下面会转为 '0' 或 '2'
 * @param {*} albumId 专辑id
 * @param {*} content 留言内容
 */
export function apiSaveLeaveMessage({ targetId, type, albumId, content }) {
  type = getMessageType( type );

  return request({
    method: 'POST',
    url: '/leavemsg/save',
    data: {
      targetId,
      type,
      albumId,
      content
    }
  }).then(result => {
    if (result.code === '0') {
      return Promise.resolve({
        type: 'success',
        data: result.data
      })
    } else {
      const message = getErrorMsg(result.code);
      return Promise.resolve({
        type: 'fail',
        message: result.message || message
      })
    }
  }).catch(err => {
    console.log( '接口错误: ', err );
  })
}

